<template>
	<div>
		<header class="g-header ">
			<div class="g-container">
				<div class="company fwb">
					<!-- 四川·眉山 -->
					<a href="/">
						四川·眉山·泷汇橡塑
					</a>
					
				</div>
				<div class="tel">
					<a href="tel:13990328171">
						联系电话: 13990328171
					</a>
				</div>
			</div>
		</header>
		<ul class="g-navs">
			<li class="active">
				首页
			</li>
			<li>
				产品列表
			</li>
			<li>
				关于我们
			</li>
			<li>
				联系我们
			</li>
		</ul>
		<div class="slides">
			<ul>
				<li v-for="(item, index) in slides" v-show="index === currentIndex">
					<img :src="item"  alt="">
				</li>
			</ul>
			<ul class="slides-dots">
				<li v-for="(item, index) in slides" :class="{active: index === currentIndex}" @click="clickSlide(index)">
				</li>
			</ul>
		</div>
		<div class="g-container  display-items">
			<div class="display-items-head df">
				<div class="">
					产品展示
				</div>
			</div>
			<ul class="display-items-content">
				<li v-for="(item, index) in itemDatas">
					<img :src="item.img_url" :alt="item.name">
					<div class="desc">{{item.name}}</div>
				</li>
			</ul>
		</div>
		<footer class="g-footer">
			<div class="g-container">
				<ul>
				<li>
				友情链接：<a href="https://www.baidu.com">百度</a> | <a href="https://www.sohu.com">搜狐</a>
				</li>
				<li>
					网站首页｜ 公司介绍｜ 供应产品｜  荣誉资质｜  联系方式
				</li>
				<li>
					©2017 泷汇橡塑 版权所有   
				</li>
				<li>
					技术支持： giovanni
				</li>
			</ul>
			</div>
			
		</footer>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				slides: ["/static/img/slides1.jpg", "/static/img/slides2.jpg", "/static/img/slides3.jpg"],
				currentIndex: 0,
				currentSlide: {
				},
				intervalId: null,
				itemDatas:[{
					img_url: "/static/img/item/51524.jpg",
					name: "四出弯滴箭"
				},{
					img_url: "/static/img/item/AOD0215.jpg",
					name: "可调式滴头"
				},{
					img_url: "/static/img/item/EL0314.jpg",
					name: "管用旁通"
				},{
					img_url: "/static/img/item/FD02120.jpg",
					name: "叠片过滤器"
				},{
					img_url: "/static/img/item/HMS02.jpg",
					name: "倒挂三件套"
				},{
					img_url: "/static/img/item/776673.jpg",
					name: "果树插杆微喷的运用"
				},{
					img_url: "/static/img/item/429747.jpg",
					name: "果树插杆微喷的运用"
				},{
					img_url: "/static/img/item/218921.jpg",
					name: "压力补偿滴头配四出弯滴箭的运用"
				},{
					img_url: "/static/img/item/341293.jpg",
					name: "倒挂式折射雾化微喷头的运用"
				},{
					img_url: "/static/img/item/MS1101A.jpg",
					name: "倒挂式折射微喷头"
				},{
					img_url: "/static/img/item/PCT0108.jpg",
					name: "可拆式压力补偿滴头"
				},{
					img_url: "/static/img/item/RS5022.jpg",
					name: "摇臂喷头"
				}]
			}
		},
		created() {
			this.runSlides();
			// this.currentSlide.url = this.slides[0];
		},
		methods: {
			runSlides() {
				this.intervalId = setInterval(()=>{
					if (this.currentIndex === this.slides.length - 1) {
						this.currentIndex = 0;
					} else {
						this.currentIndex++;
					}
				}, 3000);
			},
			clickSlide(index) {
				clearInterval(this.intervalId);
				this.currentIndex = index;
				this.runSlides();

			}
		}
	}
</script>
<style lang="less">
.g-footer{ 
	padding: 10px 0;
	background: #333;
	color: #fff;
	line-height: 30px;
	font-size: 13px;
	text-align: center;
}
	* {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
		list-style-type: none;

	}
	body, button, input, select, textarea { 
		font: 12px/1 "Hiragino Sans GB","冬青黑体",STXihei, "华文细黑", "Microsoft YaHei", "微软雅黑", SimSun, "宋体", Heiti, "黑体", sans-serif;
		color: #333;
	}
	body {
		min-width: 1000px;
	}
	img {
		max-width: 100%;
		max-height: 100%;
	}
	a {
		color: inherit;
		text-decoration: none;
	}
	.df {
		display: flex;
	}
	.fl {
		float: left;
	}
	.fr {
		float: right;
	}
	.fwb {
			font-weight: bold;
	}
	.g-container {

		width: 1000px;
		margin: 0 auto;
	}
	.g-header {
		// border-bottom: 2px solid #2b99ff;
		font-size: 32px;
	
		// background: url(../../assets/img/banner.jpg) no-repeat;
		// background-size: 100%;
		.g-container {
			display: flex;
			align-items: center;
			height: 80px;
			line-height: 80px;
			justify-content: space-between;
		}
		.company {
			font-family: cursive;
		}
		.tel {
			font-size: 16px;
		}
	}
	.g-navs {
		justify-content: space-around;
		font-size: 14px;
		
		height: 40px;
		background: #2b99ff;
		color: #fff;
		li {
			text-align: center;
			float: left;
			width: 25%;
			line-height: 40px;
			
			cursor: pointer;
			&:not(:last-child) {
				border-right: 1px solid #0e81ec;
			}
			&:hover,&.active {
				background: #0e81ec;
			}
		}
	}
	.slides {
		// height: 100px;
		position: relative;
		li {
			display: block;
			img {
				width: 100%;
			}
		}
		.slides-dots {
			position: absolute;
			width: 100%;
			bottom: 10px;
			text-align: center;
			li {
				width: 20px;
				height: 20px;
				background: #fff;
				display: inline-block;
				margin-right: 10px;
				cursor:pointer;
				&.active {
					background: #2b99ff;
				}
			}
		}
	}
	.display-items {
		font-size: 18px;
		.display-items-head {
			border-bottom: 1px solid #929292;
			line-height: 40px;
		}
		.display-items-content {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			// aligin-items: center;
			// text-align: center;
			&>li {
				margin-top: 20px;
				width: 21%;
				.desc {
					padding: 0px 5px;
					font-size: 14px;
					// margin-top: 10px;
					line-height: 25px;
					height: 60px;
					overflow: hidden;
				}
				img {
					transition: all .5s;
					width: 100%;
					height: 210px;
					border: 1px solid #ddd;
					// height: calc(100%);
					&:hover {
						transform: scale(1.07);
					}
				}
			}
		}
	}
	@media screen and (max-width: 768px) {
		body {
			min-width: 100%;
		}
		.g-header  {
			border-top: 2px solid #2b99ff;
			.g-container {
				text-align: center;
				display: block;
					line-height: 30px;
					height: auto;
					padding: 10px;
					.tel {
						font-size: 14px;
					}
			}
			font-size: 20px;
		
			.company,.tel {
				width: 100%;
			}
		}
		.g-container {
			width: 100%;
			padding: 0 20px;
		}
		.display-items-content {
			margin: 20px 0;
			display: block!important;
			&>li {
				width: 100%!important;
				display: flex;
				aligin-items: center;
				flex-direction: column;
				text-align: center;
				margin-top: 0!important;
				// height: calc(100%);
				img {
					margin: 0 auto;
					width: 300px!important;
					height: 300px!important;
				}
				.desc {
					height: 40px!important;
					line-height:  40px!important;
				}
			}
		}
		.slides-dots {
			&>li {
				width: 10px!important;
				height: 10px!important;
			}
		}
	}
</style>


